

  "use client";
  import { useState } from "react";
  import { useAuth } from "@/context/AuthContext";
  import { useQuery } from "@tanstack/react-query";
  import { api } from "@/lib/api";
  import toast from "react-hot-toast";
  import Modal from "@/components/Modal";
  import { useSocket } from "@/hooks/useSocket";
import NotificationBell from "@/components/NotificationBell";

  export default function Dashboard() {
    const { user, logout } = useAuth();
    const [openTopUp, setOpenTopUp] = useState(false);
    const [openClaim, setOpenClaim] = useState(false);
    const [amount, setAmount] = useState("");
    const [phone, setPhone] = useState(user?.phone || "");
     useSocket(user?.id, localStorage.getItem("token") || "");
    const { data, refetch } = useQuery({
      queryKey: ["transactions"],
      queryFn: async () => {
        const res = await api.get("/api/claims/history");
        return res.data.transactions;
      },
    });

    async function handleTopUp(e: React.FormEvent) {
      e.preventDefault();
      try {
        const { data } = await api.post("/api/payments/checkout", {
          phone: user?.phone,
          email: user?.email,
          nonce: `txn-${Date.now()}`,
          items: [{ price: Number(amount), quantity: 1 }],
        });
        toast.success("Redirecting to Arifpay checkout...");
        window.open(data.checkoutUrl, "_blank");
        setOpenTopUp(false);
      } catch (err: any) {
        toast.error(err.response?.data?.message || "Checkout failed");
      }
    }

    async function handleClaim(e: React.FormEvent) {
      e.preventDefault();
      try {
        await api.post("/api/claims/request", {
          amount: Number(amount),
          phone,
        });
        toast.success("Claim requested successfully");
        setOpenClaim(false);
        refetch();
      } catch (err: any) {
        toast.error(err.response?.data?.message || "Claim failed");
      }
    }

    if (!user) return <div className="text-center mt-20">Please login</div>;

    return (
      <div className="max-w-4xl mx-auto mt-12">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold">Welcome, {user.email}</h1>
          <div className="flex items-center gap-3">
            <ThemeToggle />
            <NotificationBell />
            <button onClick={logout} className="text-red-500 underline">
              Logout
            </button>
          </div>
        </div>

        <div className="mt-6 grid grid-cols-1 sm:grid-cols-2 gap-4">
          <div className="p-4 bg-white rounded shadow">
            <h2 className="text-xl font-semibold mb-2">Balance</h2>
            <p className="text-3xl text-green-600">
              {user.balance.toFixed(2)} ETB
            </p>
          </div>
          <div className="p-4 bg-white rounded shadow flex flex-col gap-2">
            <button
              onClick={() => setOpenTopUp(true)}
              className="bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
            >
              üí≥ Top Up
            </button>
            <button
              onClick={() => setOpenClaim(true)}
              className="bg-yellow-500 text-white py-2 rounded hover:bg-yellow-600"
            >
              üè• Request Claim
            </button>
          </div>
        </div>

        <div className="mt-6 p-4 bg-white rounded shadow">
          <h2 className="text-xl font-semibold mb-2">Transactions</h2>
          {data?.length ? (
            <ul>
              {data.map((t: any) => (
                <li
                  key={t.id}
                  className="border-b py-2 flex justify-between text-sm sm:text-base"
                >
                  <span>{t.type}</span>
                  <span
                    className={
                      t.type === "CREDIT" ? "text-green-600" : "text-red-500"
                    }
                  >
                    {t.amount} ETB
                  </span>
                </li>
              ))}
            </ul>
          ) : (
            <p>No transactions yet</p>
          )}
        </div>

        {/* Top Up Modal */}
        <Modal
          open={openTopUp}
          onClose={() => setOpenTopUp(false)}
          title="Top Up"
        >
          <form onSubmit={handleTopUp} className="space-y-4">
            <input
              type="number"
              placeholder="Enter amount (ETB)"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              className="w-full border p-2 rounded"
            />
            <button
              type="submit"
              className="bg-blue-600 text-white w-full py-2 rounded hover:bg-blue-700"
            >
              Proceed to Pay
            </button>
          </form>
        </Modal>

        {/* Claim Modal */}
        <Modal
          open={openClaim}
          onClose={() => setOpenClaim(false)}
          title="Request Claim"
        >
          <form onSubmit={handleClaim} className="space-y-4">
            <input
              type="number"
              placeholder="Claim amount (ETB)"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              className="w-full border p-2 rounded"
            />
            <input
              type="text"
              placeholder="Phone number (251...)"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              className="w-full border p-2 rounded"
            />
            <button
              type="submit"
              className="bg-yellow-500 text-white w-full py-2 rounded hover:bg-yellow-600"
            >
              Submit Claim
            </button>
          </form>
        </Modal>
      </div>
    );
  }
